import React from "react"
import { Link } from "gatsby"

class PostsLayout extends React.Component {
    render() {
        const data = this.props
        const posts = data.posts

        return(
            <div className="cardGrid">

                 {posts.map(({node}) => {
                    const title = node.frontmatter.title || node.fields.slug;
                    const desc = node.frontmatter.description || "";

                    return(
                        <div key={title} className="postCard">
                            <Link style={{ boxShadow: `none`, textDecoration: `none` }} to={node.fields.slug}>
                                <h3 className="cardPostTitle">{title}</h3>
                                <small className="cardPostDate">{node.frontmatter.date}</small>
                                <p className="cardPostDesc"> {desc}</p>
                            </Link>
                        </div>
                    )
                })}
            </div>
        )
    }

}
export default PostsLayout;